import React from 'react';
import './App.css';
import { Routes, Route } from 'react-router-dom';
import Blog from './components/Blog';
import Heading from './components/Heading';
import Navbar from './components/Navbar';
import Stepper from './components/Stepper';
import { FileUploadProvider } from './context/FileUploadContext';
import PdfCompressor from './components/PdfCompressor';
import PdfRotator from './components/PdfRotator';
import PdfGray from './components/Pdfgray';

function App() {
  return (
    <div className="App">
      <Navbar />
      <Routes>
        <Route path="/" element={
          <FileUploadProvider typeofFile="image">
            <>
              <Heading head='Compress JPEG & PNG images' whattocallout='Only JPG/PNG images Supported'/>
              <div className="container mt-4 mt-md-3">
                <Stepper />
              </div>
              <Blog />
            </>
          </FileUploadProvider>
        }/>
        <Route path="/pdf/pdf-compressor" element={<PdfCompressor />} />
        <Route path="/pdf/pdf-rotator" element={<PdfRotator />} />
        <Route path="/pdf/pdf-grayscale" element={<PdfGray />} />
        {/* Add more routes as necessary */}
      </Routes>
    </div>
  );
}

export default App;
