// Import necessary React hooks and components
import React, { useEffect, useState } from 'react';
import { getDocument } from 'pdfjs-dist/webpack';
// Import utility components for handling PDF files in different ways
import PdfConverter from './PdfCompressor'; // Component for compressing PDFs
import Pdfgray from './Pdfgray'; // Component for converting PDFs to grayscale
import PdfRotator from './PdfRotator'; // Component for rotating PDF pages
import { PdfTypeChecker } from './PdfTypeChecker'; // Utility for checking PDF types (not used in this component)

// Define the main PDF Viewer component
const PdfViewer = ({ uploadedPdfFile }) => {
  // State hooks for managing PDF pages, loading status, selected component, and error status
  const [pdfPages, setPdfPages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [componentToRender, setComponentToRender] = useState(null);
  const [errorOccurred, setErrorOccurred] = useState(false);

  // Function to load a PDF file and convert its pages into images
  const loadPdf = async () => {
    // Exit early if no file is uploaded or an error has previously occurred
    if (!uploadedPdfFile || errorOccurred) return;

    try {
      const pdfBuffer = await uploadedPdfFile.arrayBuffer();
      const loadingTask = getDocument({ data: pdfBuffer });

      const pdf = await loadingTask.promise;
      const pages = [];

      for (let pageNumber = 1; pageNumber <= pdf.numPages; pageNumber++) {
        const page = await pdf.getPage(pageNumber);
        const scale = 2; // Scale for rendering
        const viewport = page.getViewport({ scale });
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('2d');
        canvas.width = viewport.width;
        canvas.height = viewport.height;

        const renderContext = {
          canvasContext: context,
          viewport: viewport,
        };

        // Render page to canvas and convert to image URL
        await page.render(renderContext).promise;
        const pageUrl = canvas.toDataURL('image/jpeg');
        pages.push(pageUrl);
      }

      setPdfPages(pages);
    } catch (error) {
      console.error('Error loading PDF:', error);
      setErrorOccurred(true); // Set error flag if loading fails
    } finally {
      setLoading(false); // Indicate loading has finished
    }
  };

  // useEffect hook to load the PDF whenever a new file is uploaded
  useEffect(() => {
    setLoading(true);
    setErrorOccurred(false); // Reset error state on new file upload
    loadPdf();
  }, [uploadedPdfFile]);

  // useEffect hook to determine which component to render based on the URL path
  useEffect(() => {
    if (errorOccurred) return; // Do not process if an error has occurred

    const path = window.location.pathname.split('/').pop();

    // Select component based on the last segment of the URL path
    switch (path) {
      case 'pdf-grayscale':
        setComponentToRender(<Pdfgray uploadedPdfFile={uploadedPdfFile} />);
        break;
      case 'pdf-rotator':
        setComponentToRender(<PdfRotator uploadedPdfFile={uploadedPdfFile} />);
        break;
      case 'pdf-compressor':
        setComponentToRender(<PdfConverter uploadedPdfFile={uploadedPdfFile} />);
        break;
      default:
        setComponentToRender(null); // Reset to null if no matching path
    }
  }, [window.location.pathname, uploadedPdfFile, errorOccurred]);

  // Render error message if an error occurred during processing
  if (errorOccurred) {
    return <div>An error occurred while processing your PDF. Please try again.</div>;
  }

  // Prompt for PDF upload if no file has been uploaded yet
  if (!uploadedPdfFile) {
    return <div>Please upload a PDF file to view.</div>;
  }

  // Render the main component view
  return (
    <div style={{ width: '100%', overflowX: 'auto' }}>
      {componentToRender} {/* Render the selected component */}
      {loading && <div>Loading PDF preview...</div>} {/* Show loading message */}
      {!loading && pdfPages.length > 0 && (
        <div style={{ display: 'flex', justifyContent: 'center', flexWrap: 'wrap' }}>
          {/* Map each page to an image element */}
          {pdfPages.map((page, index) => (
            <img
              key={`page_${index}`}
              src={page}
              alt={`Page ${index + 1}`}
              style={{ margin: '10px' }}
            />
          ))}
        </div>
      )}
    </div>
  );
};

// Export the PdfViewer component for use elsewhere
export default PdfViewer;
