import React from 'react';

function Blog() {
  return (
    <div className="container blog">
      <div className="container blog mtb-4">
        <div className="row align-items-center">
          <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
            <div className="d-flex flex-column align-items-center">
              <h1><b>Simple</b> Image Compression</h1>
              <p>Just upload the image. We will do the rest in seconds.</p>
            </div>
          </div>
          <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
            <img src="/images/simple.png" class="img-fluid" alt="My Image" style={{ maxWidth: '150px', height: 'auto' }}
            />
          </div>
        </div>
      </div>

      <div className="container blog mtb-4">
        <div className="row align-items-center">
          <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
            <img src="/images/fast.png" class="img-fluid" alt="My Image" style={{ maxWidth: '150px', height: 'auto' }}
            />
          </div>
          <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
            <div className="d-flex flex-column align-items-center">
              <h1>Extremely <b>FAST</b></h1>
              <p>Get your images compressed in <i>just</i> a few seconds.</p>
            </div>
          </div>

        </div>
      </div>

      <div className="container blog mtb-4">
  <div className="row align-items-center">
    <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
      <div className="d-flex flex-column align-items-center">
        <h1>We value your<b> Privacy</b></h1>
        <p>No image is uploaded to our servers. Everything happens inside your device.</p>
      </div>
    </div>
    <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
      <img src="/images/security.png" className="img-fluid" alt="Privacy Shield" style={{ maxWidth: '200px', height: 'auto' }} />
    </div>
  </div>
</div>

<div className="container blog mtb-4">
        <div className="row align-items-center">
          <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
            <img src="/images/free.jpg" class="img-fluid" alt="My Image" style={{ maxWidth: '244px', height: 'auto' }}
            />
          </div>
          <div className="col-md-6" style={{ flex: '0 0 auto', width: '50%' }}>
            <div className="d-flex flex-column align-items-center">
              <h1>Absolutely <b>FREE</b></h1>
              <p>Get this done for absolutely <i>FREE</i>. No more paying for subscriptions!</p>
            </div>
          </div>

        </div>
      </div>

    </div>
  );
}

export default Blog;
