import React, { useState } from 'react';

function ErrorModal({ title, errorbody, onHide }) {
  // Create a state variable to track the visibility of the modal
  const [modalVisible, setModalVisible] = useState(true);

  // Function to hide the modal
  const hideModal = () => {
    setModalVisible(false);
    onHide(); // Call the onHide prop to handle modal close
  };

  return (
    // Render the modal only if modalVisible is true
    modalVisible && (
      <div className="modal fade" tabIndex="-1" role="dialog" id="errorModal">
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">{title}</h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
                onClick={hideModal} // Call the hideModal function
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <p>{errorbody}</p>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={hideModal} // Call the hideModal function
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    )
  );
}

export default ErrorModal;
