import React from 'react';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom

function Navbar() {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">
        {/* Use Link for internal navigation */}
        <Link to="/"><img className="navbar-brand" style={{ maxWidth: "100px" }} src="../logo.png" alt="Logo"/></Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            {/* Update these links to use Link as well */}
            <li className="nav-item">
              <a className="nav-link" href="/about.html">About us</a>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/">Image compressor</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/pdf/pdf-grayscale">PDF Gray scale converter</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/pdf/pdf-compressor">PDF compressor</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/pdf/pdf-rotator">PDF rotator</Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
