import React, { useState, useEffect } from 'react';
import { useFileUploadContext } from '../context/FileUploadContext'; 
import Callout from './Callout';

function Heading({head,whattocallout}) {
    var { step} = useFileUploadContext();

  const [isScaled, setIsScaled] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      setIsScaled(true);
    }, 500); // Delay of 0.5 seconds

    return () => {
      clearTimeout(timer);
    };
  }, []);

  return (
    <div className="container mt-4 mt-md-3">
        <h1 id="headingtop">{head}</h1>
      <h1 className={`animated-text ${isScaled ? 'scaled-text' : ''}`}>Quickly</h1>
      {step === 1 ? <Callout callout={whattocallout}/> : null}
    </div>
  );
}

export default Heading;
