import React, { useState, useEffect } from 'react';
import { useFileUploadContext } from '../context/FileUploadContext';
import jsPDF from 'jspdf'; // Library to work with PDFs
import { getDocument } from 'pdfjs-dist/build/pdf'; // PDF.js for loading and manipulating PDF documents
import AnimationToDownload from './AnimationToDownload'; // Component to show while processing
import DownloadButton from './DownloadButton'; // Button component for downloading the processed PDF

// Component to convert a PDF to grayscale and provide a download link
const Pdfgray = () => {
  const { uploadedFile } = useFileUploadContext(); // Access the uploaded file from context
  const [isProcessing, setIsProcessing] = useState(false); // State to track processing status
  const [downloadLink, setDownloadLink] = useState(null); // State to hold the download link

  useEffect(() => {
    if (!uploadedFile) return; // Exit if no file is uploaded

    setIsProcessing(true); // Indicate the start of processing

    const convertPDFToImagesAndPrepareDownloadLink = async () => {
      try {
        // Convert the uploaded file to a byte array for PDF.js
        const pdfData = new Uint8Array(await uploadedFile.arrayBuffer());
        const loadingTask = getDocument(pdfData);

        const pdf = await loadingTask.promise; // Await the PDF document
        let mergedPDF = null; // Variable to hold the final merged PDF

        // Loop through each page of the PDF
        for (let pageNum = 1; pageNum <= pdf.numPages; pageNum++) {
          const page = await pdf.getPage(pageNum);
          const viewport = page.getViewport({ scale: 2 }); // Get page viewport at desired scale
          const canvas = document.createElement('canvas');
          const context = canvas.getContext('2d');

          // Set canvas size as per PDF page size
          canvas.height = viewport.height;
          canvas.width = viewport.width;

          // Render the page on the canvas
          const renderContext = {
            canvasContext: context,
            viewport,
          };
          await page.render(renderContext).promise;

          // Convert the rendered page to grayscale
          const imgData = context.getImageData(0, 0, canvas.width, canvas.height);
          for (let i = 0; i < imgData.data.length; i += 4) {
            const gray = imgData.data[i] * 0.299 + imgData.data[i + 1] * 0.587 + imgData.data[i + 2] * 0.114;
            imgData.data[i] = imgData.data[i + 1] = imgData.data[i + 2] = gray; // Set RGB to gray
          }
          context.putImageData(imgData, 0, 0); // Put the modified image data back on the canvas

          // Initialize jsPDF for the first page, or add a new page for subsequent ones
          if (!mergedPDF) {
            mergedPDF = new jsPDF({
              orientation: canvas.width > canvas.height ? 'landscape' : 'portrait',
              unit: 'pt',
              format: [canvas.width, canvas.height]
            });
          } else {
            if (pageNum > 1) {
              mergedPDF.addPage([canvas.width, canvas.height], canvas.width > canvas.height ? 'landscape' : 'portrait');
            }
          }

          // Add the grayscale image to the PDF
          const imgDataURL = canvas.toDataURL('image/jpeg');
          mergedPDF.addImage(imgDataURL, 'JPEG', 0, 0, canvas.width, canvas.height, undefined, 'NONE');
        }

        // Create a blob from the merged PDF and generate a download link
        const blob = mergedPDF.output('blob');
        const downloadUrl = URL.createObjectURL(blob);
        setDownloadLink(downloadUrl); // Update state with the download link
      } catch (error) {
        console.error("Error during PDF processing:", error);
      } finally {
        setIsProcessing(false); // Indicate processing has finished
      }
    };

    // Call the function to start processing
    convertPDFToImagesAndPrepareDownloadLink();
  }, [uploadedFile]); // Effect dependencies

  // Render the appropriate component based on the current state
  return (
    <div>
      {isProcessing ? (
        <AnimationToDownload /> // Show animation while processing
      ) : downloadLink ? (
        // Show the download button when processing is complete and a download link is available
        <DownloadButton Link={downloadLink} downloadname="gray.pdf" fileType="PDF" />
      ) : null}
    </div>
  );
};

export default Pdfgray;
