// FileUploadContext.js
import React, { createContext, useContext, useState } from 'react';

const FileUploadContext = createContext();

export const FileUploadProvider = ({ children }) => {
  const [uploadedFile, setUploadedFile] = useState(null);
  const [step, setStep] = useState(1);
  const [stepper, setStepper] = useState(1);
  const [typeofFile, setTypeofFile] = useState("pic");
  const [animation, setAnimation] = useState(0);
  const [BaseFileSize, setBaseFileSize] = useState(0);
  const [FinalSize, setFinalSize] = useState(0);
  const [pdfPages, setPdfPages] = useState([]); // New state for PDF pages

  const setFile = (file) => {
    setUploadedFile(file);
    setBaseFileSize(file.size);
  };

  const updateAnimationStatus = (status) => {
    setAnimation(status);
  };


  return (
    <FileUploadContext.Provider value={{ uploadedFile, setFile, step, setStep, stepper, setStepper, typeofFile, setTypeofFile, animation, updateAnimationStatus, BaseFileSize, setBaseFileSize, FinalSize, setFinalSize, pdfPages }}>
      {children}
    </FileUploadContext.Provider>
  );
};

export const useFileUploadContext = () => {
  return useContext(FileUploadContext);
};
