// Importing necessary libraries and components from React and other sources
import React, { useEffect, useState } from 'react'
import { useFileUploadContext } from '../context/FileUploadContext'; // Import the context hook for file upload
import imageCompression from 'browser-image-compression'; // Import the library for compressing images
import { piexif } from './Exified'; // Utility for manipulating EXIF data of images
import DownloadButton from './DownloadButton'; // Component to render the download button
import AnimationToDownload from './AnimationToDownload'; // Animation shown during the download process


// Initializing global variables to hold the compressed file and its download link
var compressedFile = '';
var DownloadLink = '';
// Main functional component to handle image compression
function CompressImage() {
    // Effect hook to initiate the compression process on component mount
    useEffect(() => {
        CompressImage();
    },)
 // Destructuring necessary states and functions from the file upload context
 var { uploadedFile, setStep, animation, setFinalSize, setStepper } = useFileUploadContext();
 // Local state for managing loading status
 const [isloading, setisloading] = useState(1);
 // Local state to manage whether the download link has been generated
 const [DownloadLinkGenerated, setDownload] = useState(null);

    
    
    // Function to compress the uploaded image
    async function CompressImage() {
        // Convert PNG images to JPEG for consistency in processing
        if (uploadedFile.type === 'image/png') {
            uploadedFile = await convertPngToJpeg(uploadedFile);
        }

        console.log("Running Compressor");

        const imageFile = uploadedFile;
        console.log('originalFile instanceof Blob', imageFile instanceof Blob); // Debug log
        console.log(`originalFile size ${imageFile.size / 1024} KB`); // Debug log: original file size

        const options = {
            maxSizeMB: 0.5, // Setting the max size for the compressed image
            useWebWorker: true, // Use a web worker for compression to offload work from the UI thread
        };

        try {
            compressedFile = await imageCompression(imageFile, options);
            console.log(`compressedFile size ${compressedFile.size / 1024} KB`); // Debug log: compressed file size

            var minimumsize = 12288; // Set a minimum file size threshold (12KB)

            // If the compressed image is smaller than the minimum size, adjust its size by adding EXIF data
            if (compressedFile.size < minimumsize) {
                var requiredincrease = (minimumsize - compressedFile.size);
                console.log("Adding EXIF of Size:" + (requiredincrease));

                var modifiedBlob = await modifyexif(requiredincrease, compressedFile);
                DownloadLink = await generateDownloadLink(modifiedBlob);
                console.log(DownloadLink);

                // Update states based on whether an animation should be shown
                if (animation === 1) {
                    setFinalSize(modifiedBlob.size);
                    setisloading(0);
                    setDownload(1);
                }
            } else {
                // If no size adjustment is needed, directly generate the download link
                if (animation === 1) {
                    DownloadLink = await generateDownloadLink(compressedFile);
                    setFinalSize(compressedFile.size);
                    setisloading(0);
                    setDownload(1);
                }
            }
        } catch (error) {
            console.error(error); // Log any errors encountered during the compression process
        }
    }



    var { typeofFile } = useFileUploadContext();

    return (
        <div className="container d-flex align-items-center justify-content-center">
            {isloading === 1 ? <AnimationToDownload />  : null}
{DownloadLinkGenerated !== null ? (
    <DownloadButton
        Link={DownloadLink}
        downloadname={uploadedFile.name + "_compressed.jpeg"}
        fileType="Image" // Add this if fileType is expected in DownloadButton
        moduleType="imageCompressor" // Explicitly state the module type
    />
) : null}
        </div>
    )
}

// EXIFY START FOR ACTUALLY INCREASING SIZE.

// Function to create a downloadable link for a blob object
function generateDownloadLink(blob, fileName = "compressed_image.jpeg") {
    const link = document.createElement("a"); 
    link.href = URL.createObjectURL(blob); // Create a URL for the blob
    link.download = fileName; 
    return link.href; 
}

// Asynchronously modifies the EXIF data of a compressed file
async function modifyexif(requiredsize, compressedFile) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader(); // Initialize FileReader
        const stringSize = requiredsize; // Target size to adjust the file to
        const charToRepeat = 'a'; // Character used to pad the EXIF
        // Calculate the number of repetitions needed to achieve the required size
        const repetitions = Math.ceil(stringSize / charToRepeat.length);
        // Create the string to add to the EXIF
        const add2exif = charToRepeat.repeat(repetitions).slice(0, stringSize);

        reader.onload = function (e) {
            const imageData = e.target.result; // Resulting image data

            var exif = {}; // Initialize EXIF data object
            exif[piexif.ExifIFD.UserComment] = add2exif; // Add custom data to EXIF
            var exifObj = { "Exif": exif }; // Wrap in an object
            var exifStr = piexif.dump(exifObj); // Convert EXIF object to string
            var reader2 = new FileReader(); // Initialize another FileReader for the next operation
            reader2.onload = function (e) {
                var inserted = piexif.insert(exifStr, e.target.result); // Insert modified EXIF back
                var blomb = dataURItoBlob(inserted); // Convert the data URI back to a blob
                console.log("EXIF ADDED");
                // Create a new file object with the modified blob
                var fileobjfake = new File([blomb], "my_image.jpg", { type: "image/jpeg" });
                console.log("Size after adding EXIF: " + fileobjfake.size);
                resolve(fileobjfake); // Resolve the promise with the new file
            };
            reader2.readAsDataURL(compressedFile); // Read the compressed file as a Data URL
        };

        reader.onerror = function (e) {
            reject(e); // Reject the promise on error
        };

        reader.readAsArrayBuffer(compressedFile); // Read the compressed file as an ArrayBuffer
    });
}

// Converts a data URL to a Blob object
function dataURItoBlob(dataurl) {
    var arr = dataurl.split(','), // Split the data URL at the comma
        mime = arr[0].match(/:(.*?);/)[1], // Extract the MIME type
        bstr = atob(arr[1]), // Decode the base64 string
        n = bstr.length, // Length of the decoded string
        u8arr = new Uint8Array(n); // Initialize a new Uint8Array with the same length
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n); // Populate the array with character codes
    }
    return new Blob([u8arr], { type: mime }); // Return a new Blob object with the specified MIME type
}

// Asynchronously converts a PNG file to JPEG
async function convertPngToJpeg(pngFile) {
    const img = new Image(); // Create a new image element
    img.src = URL.createObjectURL(pngFile); // Set the source of the image to the PNG file
  
    await img.decode(); // Wait for the image to be decoded
  
    const canvas = document.createElement('canvas'); // Create a canvas element
    canvas.width = img.width; // Set the canvas width to the image width
    canvas.height = img.height; // Set the canvas height to the image height
  
    const ctx = canvas.getContext('2d'); // Get the 2D context of the canvas
    ctx.drawImage(img, 0, 0); // Draw the image onto the canvas
  
    const jpegData = canvas.toDataURL('image/jpeg', 0.85); // Convert the canvas to a JPEG data URL
  
    const jpegBlob = await fetch(jpegData).then((res) => res.blob()); // Convert the data URL to a Blob
  
    return jpegBlob; // Return the JPEG Blob
}

export default CompressImage; // Export the main function
