import React, { useEffect, useState } from 'react';
import { useFileUploadContext } from '../context/FileUploadContext';

function AnimationToDownload() {
  var [activeStep, setActiveStep] = useState(0);
  const { updateAnimationStatus } = useFileUploadContext();

  useEffect(() => {
    if (activeStep < 3) {
      const timeout = setTimeout(() => {
        setActiveStep(activeStep + 1);
      }, 850); // Adjust the delay as needed
      return () => clearTimeout(timeout); // Cleanup on unmount
    } else {
      updateAnimationStatus(1); // Animation is complete, update the context
    }
  }, [activeStep, updateAnimationStatus]);

  return (
    <div className="animation-container">
      {Array.from({ length: 3 }, (_, index) => (
        <div
          key={index}
          className={`step ${activeStep === index ? 'active' : ''}`}
        >
          {activeStep === index ? (
            <div className="step-content">
              <div className="container">
                <div className="row align-items-center">
                  {/* Add "align-items-center" class to vertically center align row content */}
                  <div className="col-sm-2">
                    <div className="lds-ring">
                      <div></div>
                      <div></div>
                      <div></div>
                      <div></div>
                    </div>
                  </div>
                  <div className="col-sm-10 p mx-auto">
                    <div className="step-text" style={{ textAlign: 'center' }}>
                      {index === 0 && 'Preparing Your File...'}
                      {index === 1 && 'Processing in Progress...'}
                      {index === 2 && 'Finalizing Details...'}

                    </div>
                  </div>
                </div>
              </div>
            </div>
          ) : null}
        </div>
      ))}
    </div>
  );
}

export default AnimationToDownload;