import React from 'react';
// Import necessary components and context
import Fileupload from './Fileupload';
import { useFileUploadContext } from '../context/FileUploadContext';
import CompressImage from './CompressImage';
import PdfConvert from './PdfCompressor';
import PdfViewer from './PdfViewer'; // Component for viewing PDFs, used in Step 2 for PDF files

// Main Stepper component
const Stepper = () => {
  // Extract step and uploadedFile from context
  const { step, uploadedFile } = useFileUploadContext();

  // Function to determine the current processing mode (PDF or image) based on the URL path
  const determineMode = () => {
    // Extract the last part of the URL path
    const lastPart = window.location.pathname.split('/').pop();
    // Return 'pdf' for paths related to PDF operations, 'image' for others
    switch (lastPart) {
      case 'pdf-compressor':
      case 'pdf-grayscale':
      case 'pdf-rotator':
        return 'pdf';
      default:
        return 'image';
    }
  };

  // Function to render content based on the current step and mode (PDF or image)
  const renderStepContent = () => {
    // Determine the current mode
    const mode = determineMode();
    // Switch case to handle different steps
    switch (step) {
      case 1: // Step 1: File upload
        // Render the Fileupload component, allowing users to upload a file
        return <Fileupload setStep={(newStep) => setStep(newStep)} />;
      case 2: // Step 2: File processing or viewing
        if (mode === 'pdf') {
          // For PDF files, render the PdfViewer component if a file is uploaded, else show a message
          return uploadedFile ? <PdfViewer uploadedPdfFile={uploadedFile} /> : <div>No PDF file uploaded.</div>;
        } else if (mode === 'image') {
          // For image files, render the CompressImage component for processing
          return <CompressImage />;
        } else {
          // Fallback for unsupported file types
          return <div>Unsupported file type.</div>;
        }
      case 3: // Step 3: Additional processing or conversion
        if (mode === 'pdf') {
          // For PDF files, render the PdfConvert component for conversion
          return <PdfConvert />;
        } else if (mode === 'image') {
          // Placeholder for additional image processing, to be implemented
          return <div>Image processing step.</div>;
        } else {
          // Fallback for unsupported file types
          return <div>Unsupported file type.</div>;
        }
      default:
        // Fallback for invalid steps
        return <div>Invalid step.</div>;
    }
  };

  // Render the stepper with the current step's content
  return (
    <div className="steps">
      <div className="message">{renderStepContent()}</div>
    </div>
  );
};

export default Stepper;
