// Import necessary dependencies and components
import React, { useState, useEffect } from 'react';
import { useFileUploadContext } from '../context/FileUploadContext';
import jsPDF from 'jspdf';
import { getDocument } from 'pdfjs-dist/build/pdf';
import AnimationToDownload from './AnimationToDownload';
import DownloadButton from './DownloadButton';

const PdfRotator = () => {
  // State for storing the loaded PDF, the download link, the rotation degrees, and the processing status
  const [pdf, setPdf] = useState(null);
  const [downloadLink, setDownloadLink] = useState(null);
  const [rotation, setRotation] = useState(0);
  const [isProcessing, setIsProcessing] = useState(false);

  // Get the uploaded file from context
  const { uploadedFile } = useFileUploadContext();

  // Effect hook to convert the uploaded PDF to images when a new file is uploaded
  useEffect(() => {
    if (!uploadedFile) return;

    const convertPDFToImages = async () => {
      const pdfData = new Uint8Array(await uploadedFile.arrayBuffer());
      const loadingTask = getDocument(pdfData);

      loadingTask.promise.then((pdf) => {
        setPdf(pdf); // Store the loaded PDF
      });
    };

    convertPDFToImages();
  }, [uploadedFile]);

  // Effect hook to merge and download the PDF when the rotation is changed
  useEffect(() => {
    // Prevents the process if downloadLink is already set, avoiding unnecessary operations
    if (rotation !== 0 && !downloadLink) {
      mergeAndDownloadPDF();
    }
  }, [rotation]);

  // Function to merge pages and download the rotated PDF
  const mergeAndDownloadPDF = async () => {
    if (!pdf) return;
  
    setIsProcessing(true); // Indicate the start of processing
  
    let mergedPDF = null;
  
    try {
      for (let pageNum = 1; pageNum <= pdf.numPages; pageNum++) {
        const page = await pdf.getPage(pageNum);
        // Apply the specified rotation to the page
        const viewport = page.getViewport({ scale: 1.5, rotation: rotation });
  
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        canvas.height = viewport.height;
        canvas.width = viewport.width;
  
        // Render the page onto a canvas
        await page.render({
          canvasContext: ctx,
          viewport: viewport,
        }).promise;
  
        if (!mergedPDF) {
          // Initialize jsPDF on the first page with the correct orientation
          mergedPDF = new jsPDF({
            orientation: canvas.width > canvas.height ? 'landscape' : 'portrait',
            unit: 'pt',
            format: [canvas.width, canvas.height]
          });
        } else {
          // Add a new page for subsequent pages
          mergedPDF.addPage([canvas.width, canvas.height], canvas.width > canvas.height ? 'landscape' : 'portrait');
        }
  
        // Add the rendered content as an image to the PDF
        const imgData = canvas.toDataURL('image/jpeg', 1.0);
        mergedPDF.addImage(imgData, 'JPEG', 0, 0, canvas.width, canvas.height, null, 'FAST');
      }
  
      // Create a download link for the merged PDF
      const blob = mergedPDF.output('blob');
      const downloadUrl = URL.createObjectURL(blob);
      setDownloadLink(downloadUrl); // Update the state with the download link
    } catch (error) {
      console.error("Error during PDF processing:", error);
    } finally {
      setIsProcessing(false); // Reset the processing indicator
    }
  };

  // Render the component UI
  return (
    <div>
      {isProcessing ? (
        // Show animation when processing
        <AnimationToDownload />
      ) : (
        <>
          {!downloadLink && ( // Show rotation options if there's no download link yet
            <>
              <div className="labeloption">
                <label>
                  <select value={rotation} onChange={(e) => setRotation(parseInt(e.target.value))}>
                    <option value="Select rotation degree" default selected>Select rotation degree</option>
                    <option value="90">90</option>
                    <option value="180">180</option>
                    <option value="270">270</option>
                  </select>
                </label>
              </div>
            </>
          )}
          {!isProcessing && downloadLink && (
            // Show the download button if there's a download link and not processing
            <DownloadButton
              Link={downloadLink}
              downloadname="compressed.pdf"
              fileType="PDF"
              moduleType="pdfRotator"
            />
          )}
        </>
      )}
    </div>
  );
};

// Export the component for use in other parts of the application
export default PdfRotator;
