import React, { useEffect, useState } from 'react';
import { useFileUploadContext } from '../context/FileUploadContext';

function DownloadButton({ Link, downloadname, fileType, moduleType }) { // Add moduleType prop
  const { setStep, FinalSize, BaseFileSize } = useFileUploadContext();
  const [reduction, setReduction] = useState(0);

  useEffect(() => {
    if (moduleType === 'imageCompressor' || moduleType === 'pdfCompressor') { // Check for specific modules
      const calculateReduction = () => {
        const calculatedReduction = 100 - (FinalSize / BaseFileSize) * 100;
        return Math.min(calculatedReduction, 99); // Ensure the reduction doesn't exceed 99%
      };

      const interval = setInterval(() => {
        const newReduction = calculateReduction();
        setReduction((prevReduction) => {
          return newReduction > prevReduction ? newReduction : prevReduction;
        });
      }, 20); // Adjust the interval duration as needed

      return () => clearInterval(interval);
    }
  }, [FinalSize, BaseFileSize, moduleType]);

  const formatFileSize = (fileSize) => {
    let size = fileSize / 1024; // Convert bytes to KB
    let unit = 'KB';

    if (size > 1000) {
      size = size / 1024; // Convert KB to MB if necessary
      unit = 'MB';
    }

    return `${size.toFixed(2)} ${unit}`;
  };

  const shouldShowDetails = moduleType === 'imageCompressor' || moduleType === 'pdfCompressor';

  return (
    <div>
      <a
        id="DownButton"
        className="DownButton btn btn-primary btn-lg mb-1"
        href={Link}
        target="_blank"
        rel="noopener noreferrer"
      >
        <i className="fa-solid fa-circle-chevron-down"></i> Open {fileType}
      </a>
      <br />
      {shouldShowDetails && (
        <div className="Worked mb-1">
          <i className="fa-solid fa-check" style={{ color: 'black' }}></i>
          <span className="p-2">
            {formatFileSize(BaseFileSize)} {'>'} {formatFileSize(FinalSize)} - 
            <span style={{ color: 'red' }}>{reduction.toFixed(0)}%</span> Reduction!
          </span>
          <br />
          <i className="fa-solid fa-check" style={{ color: 'black' }}></i>
          <span className="p-2">Set Width and Height Accordingly.</span>
        </div>
      )}
      <br/>
      <a
        id="CompressAgain"
        className="CompressAgain btn btn-primary btn-lg"
        onClick={() => setStep(1)}
      >
        <i className="fa-solid fa-arrow-rotate-left"></i> Upload Another File
      </a>
    </div>
  );
}

export default DownloadButton;
