import React, { useState, useEffect } from 'react';
import { useFileUploadContext } from '../context/FileUploadContext';
import jsPDF from 'jspdf';
import { getDocument } from 'pdfjs-dist/build/pdf';
import AnimationToDownload from './AnimationToDownload';
import DownloadButton from './DownloadButton'; // Make sure the path is correct
import ErrorModal from './ErrorModal';

const PdfCompressor = () => {
  const [downloadLink, setDownloadLink] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [showErrorModal, setShowErrorModal] = useState(false);
  const { uploadedFile, setBaseFileSize, setFinalSize, setStep } = useFileUploadContext();

  useEffect(() => {
    const compressAndGeneratePDF = async () => {
      if (!uploadedFile) return;

      const originalSizeBytes = uploadedFile.size;
      setIsLoading(true); // Start the animation when compression starts

      const pdfData = new Uint8Array(await uploadedFile.arrayBuffer());
      const loadingTask = getDocument(pdfData);
      try {
        const pdf = await loadingTask.promise;
        const mergedPDF = new jsPDF();

        for (let pageNum = 1; pageNum <= pdf.numPages; pageNum++) {
          const page = await pdf.getPage(pageNum);
          const viewport = page.getViewport({ scale: 1.5 });
          const canvas = document.createElement('canvas');
          const context = canvas.getContext('2d');
          canvas.height = viewport.height;
          canvas.width = viewport.width;

          const renderContext = {
            canvasContext: context,
            viewport: viewport,
          };

          await page.render(renderContext).promise;

          const imgData = canvas.toDataURL('image/jpeg');
          if (pageNum === 1) {
            mergedPDF.addImage(imgData, 'JPEG', 0, 0, 210, (210 / viewport.width) * viewport.height);
          } else {
            mergedPDF.addPage();
            mergedPDF.addImage(imgData, 'JPEG', 0, 0, 210, (210 / viewport.width) * viewport.height);
          }
        }

        const pdfBlob = mergedPDF.output('blob');
        const compressedSizeBytes = pdfBlob.size;

        // Check if the compressed size is greater than the original
        if (compressedSizeBytes > originalSizeBytes) {
          console.error('Compressed file is larger than the original file.');
          const errorModal = document.getElementById('errorModal');
          if (errorModal) {
            errorModal.classList.add('show');
            errorModal.style.display = 'block';
            errorModal.setAttribute('aria-modal', 'true');
          }
          return 0;
        }

        console.log(`Processed file size: ${compressedSizeBytes} bytes`);
        setFinalSize(compressedSizeBytes);

        const url = URL.createObjectURL(pdfBlob);
        setDownloadLink(url);

      } catch (error) {
        console.error("Error loading PDF:", error);
      } finally {
        setIsLoading(false); // Stop the animation once PDF is ready or if an error occurs
      }
    };

    compressAndGeneratePDF();
  }, [uploadedFile, setBaseFileSize, setFinalSize]);

  return (
    <>
      <div className="container d-flex align-items-center justify-content-center">
        {isLoading ? (
          <AnimationToDownload />
        ) : downloadLink ? (
          <DownloadButton
            Link={downloadLink}
            downloadname="compressed.pdf"
            fileType="PDF"
            moduleType="pdfCompressor"
          />
        ) : null}
      </div>
      <ErrorModal
  title="Error"
  errorbody="Unable to compress this PDF further."
  onHide={() => setShowErrorModal(false)}
/>
    </>
  );
};

export default PdfCompressor;
